import Vue from 'vue'
import CONSTANT from '../data/constant'
import PGET from '../js/public-static-get'
import store from '../store'
import dragDrop from '../js/drag-drop'
import moveClass from '../js/different-item-move-class'

export default function(el, binding) {

  let { hero, position } = binding.value
  
  let event = {
    dragend(event) {
      event.dataTransfer.clearData("text")
      return false
    },
    dragover(event) {
      event.preventDefault()
      return true
    },
    dragstart(event) {
      event.dataTransfer.setData("text", position)
      try {
        event.dataTransfer.setDragImage(!~event.target.className.indexOf("package-item") ? event.target.parentNode : event.target, 20, 20)
      } 
      catch(error) {
        // pass
      }
      let itemPover = document.querySelector('.item-tip-pover')
      itemPover && itemPover.parentNode.removeChild(itemPover)
    },
    drop(event) {
      event.preventDefault()

      dragDrop(
        event.dataTransfer.getData("text"), 
        position, 
        hero
      )

      hero.updateHp()

      store.commit('UPDATE')
    }
  }

  // 
  {
    let block = new moveClass(position).get()

    if(block) el.setAttribute('draggable', 'true')
    else el.removeAttribute('draggable')

    block = null
  }

  // 
  for(let key in event) {
    let value = event[key], keyNameInElement = `${key}_EVENT_FUNCTION_ITEM_BLOCK`
    el.removeEventListener(key, el[keyNameInElement])
    el.addEventListener(key, value)
    el[keyNameInElement] = value
  }
}