import { RESUME_ITEMS_TABLE } from '../data/item-data'

const CHEST = 'map-chest', FIRST_ID = RESUME_ITEMS_TABLE[0].id

const getChestData = function() {
  var chestList = []
  for(let item of RESUME_ITEMS_TABLE) {
    chestList.push({
      $type: CHEST,
      id: 5000000 + (item.id - FIRST_ID + 1),
      data: [
        {
          msg: '',
          buttons: [
            "[]{3, 1}",
            "#[]{2, 1}"
          ],
          get: [[item.id, 1]]
        },
        '',
        '',
        ''
      ]
    })
  }
  return chestList
}

const CHEST_DATA = getChestData()

export default CHEST_DATA